import {Avatar, AvatarInitial, Dropdown, DropdownToggle} from "@doar/components";
import {Edit2, ArrowRight, MoreHorizontal, Plus} from "react-feather"
import {
    Chip,
    ProductItemsContainer,
    SecondContainer,
    StyledTable, StyledTableItemsContainer,
    StyledTableTD1, StyledTableTD2, StyledTableTD3, StyledTableTD4, StyledTableTD5, StyledTableTD6,
    StyledTableTH1,
    StyledTableTH2,
    StyledTableTH3,
    StyledTableTH4,
    StyledTableTH5, StyledTableTH6
} from "./style";
import {
    StyledDropMenu,
    StyledDropHeader,
    StyledDropItem
} from '../header/header-dropdown-elements'
import {Container, Row,} from '../signup-form/style'
import {Link} from 'react-router-dom'
import StyledTitleComponent from "../StyledTitleComponent";
let ProductListContainer = () =>{
    return (
        <div>
            <StyledTitleComponent
                title='Product List'
                backText={<Container align='center' ><Plus size={15} />  Add product </Container>}
                buttonColor='success'
                buttonUrl='/product/create'
            />
            <br/>
            <StyledTable>
                <StyledTableTH1> Image</StyledTableTH1>
                <StyledTableTH2>Product Name</StyledTableTH2>
                <StyledTableTH3>Category</StyledTableTH3>
                <StyledTableTH4>Narx</StyledTableTH4>
                <StyledTableTH5>Status</StyledTableTH5>
                <StyledTableTH6>Actions</StyledTableTH6>
            </StyledTable>
            <StyledTableItemsContainer>
               <SecondContainer>
                   {products.map((one)=><ProductItemsContainer>
                       <StyledTableTD1 key={one.id}>
                           <Avatar size='sm'>
                               <AvatarInitial bg="gray700" fontWeight={400}>
                                   A
                               </AvatarInitial>
                           </Avatar>
                        </StyledTableTD1>
                       <StyledTableTD2>
                            {one.name}
                       </StyledTableTD2>
                       <StyledTableTD3>
                           {one.category}
                       </StyledTableTD3>
                       <StyledTableTD4>
                            {one.price}
                       </StyledTableTD4>
                       <StyledTableTD5>
                            <Chip bg='#10b759'>{one.status}</Chip>
                       </StyledTableTD5>
                       <StyledTableTD6>
                            <Container>
                                <Row sm='25' md='25' lg='25' xl='25'>
                                    <Dropdown direction="down">
                                        <DropdownToggle variant="texted">
                                            <MoreHorizontal  size={15} />
                                        </DropdownToggle>
                                        <StyledDropMenu>
                                            <StyledDropHeader>Status Change</StyledDropHeader>
                                            <StyledDropItem path="/profile-view">
                                                Status 1
                                            </StyledDropItem>
                                            <StyledDropItem path="/profile-view">
                                                Status 2
                                            </StyledDropItem>

                                        </StyledDropMenu>
                                    </Dropdown>
                                </Row>
                                <Row sm='25' md='25' lg='25' xl='25'>
                                    <Link to={`/product/${one.id}`}>
                                        <Edit2 size={15} />
                                    </Link>

                                </Row>
                                <Row sm='25' md='25' lg='25' xl='25'>
                                    <Link to={`/product/${one.id}`}>
                                        <ArrowRight size={15} />
                                    </Link>

                                </Row>
                            </Container>
                       </StyledTableTD6>
                   </ProductItemsContainer>
                   )}
               </SecondContainer>
            </StyledTableItemsContainer>
        </div>
    )
}

export default ProductListContainer


